      </div>
      <!-- ITEMS_NAV__SUPER ADMIN -->
      <router-link class="item-nav" :class="{active: title === 'Restaurantes'}" to="/">Restaurantes</router-link>
      <v-divider></v-divider>
      <router-link class="item-nav" :class="{active: title === 'Chats'}" to="/chat">Chats</router-link>
    </v-navigation-drawer>

    <v-navigation-drawer id="menuL" v-model="drawerL" absolute temporary v-if="user.type === 'Reception'">
      <div class="button-sec">
        <v-btn icon class="close-ico" @click="drawerL = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </div>
