      </router-link>
    </v-navigation-drawer>


    <v-navigation-drawer id="menuL" v-model="drawerL" absolute temporary v-if="user.type === 'Cocina'">
      <div class="button-sec">
        <v-btn icon class="close-ico" @click="drawerL = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </div>
      <!-- ITEMS_NAV__RECEPTION-->
      <router-link class="item-nav" :class="{active:title === 'Órdenes'}" to="/cocina/orders">Órdenes</router-link>
      <v-divider class="invisible m-4"></v-divider>
      <router-link class="item-nav" :class="{active:title === 'Historial'}" to="/recepcion/history">Historial
      </router-link>
      <v-divider class="invisible m-4"></v-divider>
      <router-link class="item-nav" :class="{active:title === 'Informe'}" to="/cocina/report">Informe
      </router-link>
      <v-divider></v-divider>
    </v-navigation-drawer>

    <!-- Navigation right  -->

    <v-navigation-drawer id="menuR" v-model="drawerR" right absolute temporary>
      <div class="button-secR">
        <v-btn icon class="close-ico" @click="drawerR = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>

        <div class="row infoP">
          <div class="col-4">
            <img src="@/assets/Images/logo-side.png" alt="cityhub-logo" />
          </div>
          <div class="col-8">
            <h3>{{user.name}}</h3>
            <h5>{{user.type}}</h5>
          </div>
        </div>

        <v-divider></v-divider>

        <div class="row logout" @click="actionLogout">
          <div class="col-12 text-left">
            <img src="@/assets/Images/external-link.png" alt="external-link" />
            <p>Cerrar sesión</p>
          </div>
        </div>

        <v-divider></v-divider>
      </div>
    </v-navigation-drawer>
    <LogoutDialog @hide="hide" @deactive="logout" :active="logoutDialog"/>
  </div>
</template>

<script>
  import {
    mapState
  } from "vuex";
  import LogoutDialog from '@/components/LogoutDialog.vue';
  import Parse from "parse"

  export default {
    props: {
      isAuthenticated: Boolean,
    },
    data: () => ({
      drawerL: false,
      drawerR: false,
      logoutDialog: false,
      buttons: [{
          key: 0,
          text: "Activo",
          active: true,
          quantity: 1,
          path: "/recepcion"
        },
        {
          key: 1,
          text: "Entregado",
          active: false,
          quantity: 2,
          path: "/recepcion/done"
        },
      ],

    }),
    components: {
      LogoutDialog,
    },
    computed: {
      title() {
        return this.$store.state.title;
      },
      ...mapState(['user']),
    },
    methods: {
      actionLogout() {
        this.logoutDialog = true
      },
      activeButton(key) {
        for (const item of this.buttons) {
          if (key === item.key) {
            item.active = true;
            this.$router.push(item.path)
          } else {
            item.active = false;
          }
        }
      },
      logout(){
        this.logoutDialog = false
        Parse.User.logOut().then(() => {
          var currentUser = Parse.User.current();
          if(currentUser ===  null){
            this.$eventBus.$emit('message','Hasta luego')
            this.$store.commit("SET_USER", null);
            this.$router.push("/login")
          }else{
            this.$eventBus.$emit('message','Ha ocurrido un error')
          }
        });
      },
      hide() {
        this.logoutDialog = false
      }
    },
  };
</script>

