import Vue from 'vue'
import VueRouter from 'vue-router'
import Parse from 'parse'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Home',
    component: () => import( /* webpackChunkName: "restaurant" */ '../views/Restaurants.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import( /* webpackChunkName: "login" */ '../views/Login.vue')
  },
  {
    path: '/notificaciones',
    name: 'Notifications',
    component: () => import( /* webpackChunkName: "notificaciones" */ '../views/Notifications.vue')
    ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/restaurant:id',
    name: 'Restaurant',
    component: () => import( /* webpackChunkName: "detalle" */ '../views/Detail.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/chat',
    name: 'Chats',
    component: () => import( /* webpackChunkName: "chat" */ '../views/Chats.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/order',
    name: 'Order',
    component: () => import( /* webpackChunkName: "order" */ '../views/Order.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/recepcion',
    name: 'Recepcion',
    component: () => import( /* webpackChunkName: "recepcion" */ '../viewsRecepcion/Recepcion.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/recepcion/done',
    name: 'Done',
    component: () => import( /* webpackChunkName: "done" */ '../viewsRecepcion/Done.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/recepcion/order:id/domi',
    name: 'SelectDomi',
    component: () => import( /* webpackChunkName: "selectDomi" */ '../viewsRecepcion/Domi.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/recepcion/history',
    name: 'History',
    component: () => import( /* webpackChunkName: "history" */ '../viewsRecepcion/History.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/recepcion/domis',
    name: 'Domis',
    component: () => import( /* webpackChunkName: "domis" */ '../viewsRecepcion/Domis.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/cocina/orders',
    name: 'CocinaOrders',
    component: () => import( /* webpackChunkName: "cocinaorders" */ '../viewsCocina/Orders.vue') ,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/cocina/report',
    name: 'CocinaReport',
    component: () => import( /* webpackChunkName: "cocinareport" */ '../viewsCocina/Report.vue') ,
    meta: {
      requiresAuth: true
    }
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, _, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    var currentUser = Parse.User.current();
    if (!currentUser) {
      next({
        name: 'Login',
        query: { redirect: to.fullPath }
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router