<template>
  <v-app >
    <Navbar :isAuthenticated="isAuthenticated" />
    <v-main>
      <router-view></router-view>
      <v-snackbar 
      :right="true" 
      v-for="(message, index) in messages" 
      v-model="message.activate" 
      :key="index">
      {{ message.text }}
      <v-btn color="pink" flat @click="removeMessage(index)">
        Cerrar
      </v-btn>
    </v-snackbar>
    </v-main>
  </v-app>
</template>

<script>
  import Navbar from "./components/Essential/Navbar";

  export default {
    name: "App",

    data() {
      return {
        messages: [],
      }
    },

    components: {
      Navbar,
    },
    computed: {
      isAuthenticated() {
        return this.$store.state.user != null;
      },
    },

    created() {
      this.$eventBus.$on('message', text => {
        this.messages.push({
          text: text,
          activate: true
        })
      })
    },

    methods: {
      removeMessage(index) {
        this.messages[index].activate = false
        this.messages.splice(index, 0)
      }
    }
  };
</script>

