<template>
  <v-row justify="center">

    <v-dialog
      v-model="active"
      max-width="420"
    >
      <v-card>
        <v-card-title class="headline">
          Cerrar sesión
        </v-card-title>

        <v-card-text class="" style="font-size:1rem">
          {{text}}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="red darken-1"
            text
            @click="hide"
          >
            Cancelar
          </v-btn>

          <v-btn
            color="red darken-1"
            dark
            @click="destroyObject"
          >
            Salir
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
  export default {
    props: ['active'],
    data () {
      return {
        loading: false
      }
    },
    computed: {
      text() {
        return (this.loading) ? 'Saliendo...': '¿Está seguro que desea salir?'
      }
    },
    methods: {
       destroyObject() {
        this.$emit('deactive')
      },
      hide() {
        this.$emit('hide')
      }
    }
  }
</script>